.App {
  background-color: #282c34;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  color: white;
  font-size: calc(10px + 1vmin);
  margin-left: 1vw;
  padding-bottom: 1vw;
}

.Section {
  margin: 1vw;
  border-radius: 10px;
  border-width: 10px;
  border-color: white;
}

.Section-title {
  text-align: center;
}

li {
  margin-left: 4vw;
}



